import axios from 'axios'
import { Toast } from 'vant'
import store from '@/store'

// 创建 axios实例, 将来对创建出来的实例可以进行自定义配置
// 好处: 不会污染原始的 axios实例
const instance = axios.create({
  // https://apifoxmock.com/m1/4928016-4585237-default/data
  // baseURL: 'http://smart-shop.itheima.net/index.php?s=/api/', // 基地址
  baseURL: 'http://smart-shop.itheima.net/index.php?s=/api/', // 基地址
  timeout: 6000
  // headers: { 'X-Custom-Header': 'foobar' }
})

// 自定义配置: 请求/相应拦截器

// 1.添加请求拦截器
instance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // 开启loading, 禁止背景点击, 避免加载时请求的无效触发
  Toast.loading({
    message: '加载中...',
    forbidClick: true, // 禁止背景点击
    loadingType: 'spinner', // 配置loading图标
    duration: 0 // 0为不自动消失(需写代码取消), 其他数字是展示时长
  })

  // 只要有token, 就在请求时携带(便于请求需要授权的接口)
  const token = store.getters.token
  if (token) {
    config.headers['Access-Token'] = token
    config.headers.platform = 'H5'
  }

  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 2.添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么(默认axios会多包装一层data, 此处进行响应拦截器处理一下)
  const res = response.data
  if (res.status !== 200) {
    // 给错误提示, Toast 默认是单例模式, 后面的 Toast调用了, 会将前一个 Toast 效果覆盖
    // 同时只能存在一个 Toast, 这里的Toast会替换掉loading, 所以不用处理
    Toast(res.message)
    // 抛出一个错误的promise
    return Promise.reject(res.message)
  } else {
    // 即200(正确情况), 走业务核心逻辑
    // 清除loading效果
    Toast.clear()
  }
  return res
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  return Promise.reject(error)
})

// 导出配置好的实例
export default instance
