import request from '@/utils/request'

// 订单结算确认
// mode: cary     => obj { cartIds }
// mode: buyNow   => obj { goodsId goodsNum goodsSkuId}
export const checkOrder = (mode, obj) => {
  return request.get('/checkout/order', {
    params: {
      mode, // 可以传cart或者buyNow 从而决定发送哪种格式的请求
      delivery: 10, // 10 快递配送 20 门店自取
      couponId: 0, // 优惠券ID 传0为不使用优惠券
      isUsePoints: 0, // 积分 0为不使用积分
      ...obj
    }
  })
}

// 提交订单
// mode: cary     => obj { cartIds remark留言 }
// mode: buyNow   => obj { goodsId goodsNum goodsSkuId remark}
export const submitOrder = (mode, obj) => {
  return request.post('/checkout/submit', {
    mode, // 可以传cart或者buyNow 从而决定发送哪种格式的请求
    delivery: 10, // 10 快递配送 20 门店自取
    couponId: 0, // 优惠券ID 传0为不使用优惠券
    isUsePoints: 0, // 积分 0为不使用积分
    payType: 10, // 余额支付
    ...obj
  })
}

// 订单列表
export const getMyOrderList = (dataType, page) => {
  return request.get('/order/list', {
    params: {
      dataType, // 订单的类型
      page // 用vant的List做分页
    }
  })
}
