// 本地存储模块

// 约定一个通用的键名
const INFO_KEY = 'hm_shopping_info'
const HISTORY_KEY = 'hm_history_list'
const CART_KEY = 'hm_shopping_cart'
const CARTTOTAL_KEY = 'hm_shopping_carttotal'

// 获取个人信息
export const getInfo = () => {
  const defaultObj = { token: '', userId: '' } // 默认值
  const result = localStorage.getItem(INFO_KEY)
  return result ? JSON.parse(result) : defaultObj
}

// 设置个人信息
export const setInfo = (obj) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(obj))
}

// 移除个人信息
export const removeInfo = () => {
  localStorage.removeItem(INFO_KEY)
}

// 获取购物车信息
export const getCart = () => {
  const result = localStorage.getItem(CART_KEY)
  return result ? JSON.parse(result) : []
}

// 设置购物车信息
export const setCart = (obj) => {
  localStorage.setItem(CART_KEY, JSON.stringify(obj))
}

// 移除购物车信息
export const removeCart = () => {
  localStorage.removeItem(CART_KEY)
}

// 获取搜索历史
export const getHistoryList = () => {
  const result = localStorage.getItem(HISTORY_KEY)
  return result ? JSON.parse(result) : []
}

// 设置搜索历史
export const setHistoryList = (arr) => {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(arr))
}

// 获取购物车角标
export const getCartTotal = () => {
  const result = localStorage.getItem(CARTTOTAL_KEY)
  return result ? JSON.parse(result) : 0
}

// 设置购物车角标
export const setCartTotal = (cartTotal) => {
  localStorage.setItem(CARTTOTAL_KEY, JSON.stringify(cartTotal))
}
