// 购物车仓库

import { delSelect, changeCount, getCartList } from '@/api/cart'
import { Toast } from 'vant'
import { setCart, getCart, setCartTotal } from '@/utils/storage'

export default {
  namespaced: true,
  state () { // 仓库的数据都付state
    return {
      cartList: getCart()
    }
  },
  mutations: { // 组件只能让仓库自己修改仓库的数据
    // 提供一个设置 cartList 的 mutation
    setCartList (state, newList) { // 所有mutations的第一个参数都是state
      state.cartList = newList
      setCart(newList)
    },
    toggleCheck (state, goodsId) {
      // 让对应id的项状态取反
      const goods = state.cartList.find(item => item.goods_id === goodsId) // find返回满足条件的第一个item, filter返回满足条件的所有item组成的数组
      goods.isChecked = !goods.isChecked
    },
    toggleAllCheck (state, flag) {
      // 让所有的小选框, 同步设置
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    // 由+-组件带来的数据更新(从而更新商品的goods_num)
    chageCount (state, { goodsId, goodsNum }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    }
  },
  actions: { // 处理异步
    async getCartAction (context) {
      const { data } = await getCartList()
      // 返回后台的数据中, 不包含复选框的选中状态, 需手动添加isChecked是否选中
      data.list.forEach(item => {
        item.isChecked = false
      })
      context.commit('setCartList', data.list) // context.commit用于提交 mutation 的方法
    },
    async changeCountAction (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      // 1.本地修改
      context.commit('chageCount', { goodsId, goodsNum })
      // 2.同步到后台
      const goodsCartTotal = await changeCount(goodsId, goodsNum, goodsSkuId)
      setCartTotal(goodsCartTotal.data.cartTotal)
    },

    // 删除购物车数据
    async delSelect (context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map(item => item.id) // 新数组
      const goodsCartTotal = await delSelect(cartIds)
      setCartTotal(goodsCartTotal.data.cartTotal)
      Toast('删除成功')

      // 重新拉取最新的购物车数据 (重新渲染)
      context.dispatch('getCartAction') // context.dispatch用于分发actions的方法
    }
  },
  getters: { // 数据处理(类似于计算属性)
    // 求所有的商品累加总数
    cartTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的商品项
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked) // 筛选数组中isChecked为true的 返回新数组
    },
    // 选中的总数
    selCount (state, getters) { // getters中用getters的用法
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的总价
    selPrice (state, getters) { // getters中用getters的用法
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num * item.goods.goods_price_min, 0)
        .toFixed(2) // 保留两位小数
    },
    // 是否全选
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked) // 检测数组中的所有元素是否都符合指定条件 返回true false
    }
  }
}
