// 配置路由
import Vue from 'vue'
import VueRouter from 'vue-router'
// 写文件夹会自动找到index.vue文件
import Layout from '@/views/layout'
import Login from '@/views/login'
import Search from '@/views/search'
import Searchlist from '@/views/search/list'
import Prodetail from '@/views/prodetail'
import Pay from '@/views/pay'
import Myorder from '@/views/myorder'

import Home from '@/views/layout/home'
import Category from '@/views/layout/category'
import Cart from '@/views/layout/cart'
import User from '@/views/layout/user'

import store from '@/store'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { // 首页
      path: '/',
      component: Layout,
      redirect: '/home',
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: Category },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }

      ]
    },
    { path: '/login', component: Login }, // 登录页
    { path: '/search', component: Search }, // 搜索页
    { path: '/searchlist', component: Searchlist }, // 搜索列表
    // 动态路由传参
    { path: '/prodetail/:id', component: Prodetail }, // 商品详情页
    { path: '/pay', component: Pay }, // 结算支付页
    { path: '/myorder', component: Myorder } // 订单管理页
  ]
})

// 所有的路由解析渲染之前, 都会先经过全局前置守卫

// 定义一个数组, 专门存放需要权限访问的页面地址
const authUrls = ['/pay', '/myorder']

// to: 到哪里去, 到哪里去的完整路由信息对象 (路径,参数)
// from: 从哪里来, 从哪里来的完整路由信息对象 (路径,参数)
// next(): 是否放行
// (1) next() 直接放行,放行到to
// (2) next(路径) 进行拦截,拦截到括号里的路径

router.beforeEach((to, from, next) => {
  // 看to.path(不带参数) 是否在 authUrls(权限访问地址) 中出现过
  if (!authUrls.includes(to.path)) {
    // console.log(to, from, next)
    next()
    return
  }

  const token = store.getters.token
  if (token) {
    next()
  } else {
    next('/login')
  }
})

export default router
